import { Typography } from '@mui/material';
import { isMobile } from 'react-device-detect';
import DownloadButton from './download/DownloadButton';
import onShare from './share/onShare';
import ShareButton from './share/ShareButton';

export default function Header() {
    return (
        <>

            <Typography
                variant="h8"
                onClick={onShare}
            >
                {`להדלקת נר וירטואלי לחץ כאן ושתף`}
            </Typography>
            {
                isMobile ?
                    <ShareButton />
                    :
                    <DownloadButton />
            }
            <Typography
                variant="h8"
                textAlign="right"
            >
                {`ניתן להוסיף לנר את שם יקירכם`}
            </Typography>
        </>
    );
}