import { Box, Link, Typography } from '@mui/material';

const margin = 3;

export default function MoreInfo() {
    return (
        <>
            <Box
                sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    bgcolor: "#f4edd5",
                }}
            >
                <Typography
                    style={{
                        fontFamily: 'Assistant'
                    }}
                    variant="h6"
                    component="div"
                >
                    בקצרה על שבוע המודעות
                </Typography>
                <Typography
                    style={{
                        fontFamily: 'Assistant'
                    }}
                    variant="h6"
                    component="div"
                >
                    לשכול האזרחי
                </Typography>
                <Typography
                    variant="h8"
                    component="div"
                >
                    כל אחד מאיתנו מכיר לפחות משפחה אחת שחוותה שכול של בן משפחה צעיר ממחלה, תאונה או אסון טרגי
                </Typography>
                <Typography
                    variant="h8"
                    component="div"
                >
                    למרבה הכאב, משפחות אלו נשארות לבדן בצערן, ללא מערך תמיכה והכרה מוסדר וללא יום לאומי ממלכתי מחבק ומאחד
                </Typography>
                <Typography
                    variant="h8"
                    component="div"
                    sx={{ mb: margin }}
                >
                    את הכאב שבאובדן לא ניתן לרפא, אך אנו מאמינים שביכולתה של הקהילה והחברה להקל מעט על הכאב והבדידות שחוות המשפחות השכולות.
                    וזוהי מטרת שבוע המודעות לשכול האזרחי
                </Typography>

                <Link
                    href="https://yakirli.org/awareness-week/"
                >
                    למידע נוסף אודות שבוע המודעות לשכול האזרחי
                </Link>
            </Box>
        </>
    )
}
